<template>
  <div class="home">
    <el-container>
      <el-header height="70px">
        <!-- <my-header></my-header> -->
        <!-- 页面头部信息 -->
        <div class="header">
          <div class="header-icon" @click="expand">
            <span class="icon"></span>
            <div>数字证书管理系统</div>
          </div>
          <div class="header-tool" id="user">
            <el-dropdown>
              <!-- <router-link :to="{ path: '/information:id',query: {id: this.UserInfo.id} }"> -->
              <span class="tool-one" @click="info">
                  Hello，{{UserInfo.username}}</span>
                  <!-- </router-link> -->
              <!-- 鼠标移到名字的位置可以选择退出系统 -->
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item @click.native="go">
                  退出系统
                </el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </div>
      </el-header>
      <el-container>
        <el-aside v-if="getAsideType !== 0" :width="getAsideType === 1 ? '200px' : '48px'">
          <div class="meau">
            <li @click="homepage">首页</li>
            <li @click="pro">项目管理</li>
            <!-- <li @click="sess">项目-届次</li> -->
            <li @click="cert">证书管理</li>
            <li @click="des">自定义证书</li>
            <li @click="backsta" v-if="hide">后台管理</li>
            <li @click="info">个人信息</li>
          </div>
          <!-- <com-aside :datas="datas">
            ndavm
          </com-aside> -->
        </el-aside>
        <el-container>
          <!-- 页面中部，使用router方式 -->
          <el-main style="height: calc(100% - 78px);">
            <router-view />
          </el-main>
        </el-container>
      </el-container>
    </el-container>
  </div>
</template>

<script>
// import ComAside from "@/components/ComAside";
// import myHeader from '@/components/header'
export default {
  name: "Home",
  components: {
    // comAside: ComAside,
    // myHeader
  },
  data() {
    return {
      datas: [],
      UserInfo: {
        id: "",
        password: "",
        username: "李赫海",
        phone: "",
      },
      index1: 0, //设置默认index值
      show: false, //隐藏提示框
      hide: true, //隐藏后台
    };
  },
  watch: {
    // 监听路由
    $route: {
      immediate: true, // 立即的，会先于create开启监听，在组件加载时就调用一次
      deep: true, // 开启深遍历，监听内部变化
      handler() {
        //  获取输入框内容，定位展开位置
        this.$store.commit("setInputValue", this.$route.query.inputValue);
      },
    },
  },
  computed: {
    getAsideType() {
      return this.$store.state.asideType;
    },
  },
  async mounted() {
    // 获取datas
    let datas = () => {
      return import("@/utils/datas.json");
    };
    this.datas = JSON.parse(JSON.stringify(await datas())).default;
    this.UserInfo.id = this.$route.query.id;
    // this.show_hide();
  },
  // inject:["reload"],
  methods: {
    //隐藏后台管理
    // show_hide(){
    //   if(this.UserInfo.id =='admin'){
    //     this.hide=false;
    //   }
    // },
    //退出系统
    go(){
      this.$router.push({ path: '/'})
    },
    //返回主页
    homepage(){
      this.$router.push({ path: '/homepage:id' ,query: {id:this.UserInfo.id}})
      // this.reload();
    },
    //来到项目管理
    pro(){
      this.$router.push({ path: '/project:id' ,query: {id:this.UserInfo.id}})
      // this.reload();
    },
    //来到证书管理
    cert(){
      this.$router.push({ path: '/certificate:id' ,query: {id:this.UserInfo.id}})
      // this.reload();
    },
    des(){
      this.$router.push({ path: '/design:id' ,query: {id:this.UserInfo.id}})
      // this.reload();
    },
    //来到后台管理
    backsta(){
      this.$router.push({ path: '/backstage:id' ,query: {id:this.UserInfo.id}});
      // this.reload();
    },
    //来到个人信息管理
    info(){
      this.$router.push({ path: '/information:id' ,query: {id:this.UserInfo.id}});
      // this.reload();
    },
    
  },
};
</script>

